export default "/*\n    Colors\n*/\n/*\n    fonts\n*/\n/*\n    Media query\n*/\n/*\n    Spacing\n*/\n/*\n    Globals\n*/\n:host {\n  display: block;\n  border: 1px solid #adb8c557;\n  padding: 33px;\n  border-bottom-width: 2px;\n  margin-bottom: 30px;\n}\n.meta {\n  display: flex;\n  font-family: \"Roboto Mono\";\n  font-size: 12px;\n  text-transform: uppercase;\n}\n.separator {\n  color: #0094ff;\n  margin: 0 10px;\n  font-weight: 700;\n}\n.title {\n  font-size: 1.7em;\n  line-height: 1.25;\n  color: #020814;\n  margin-bottom: 20px;\n}\n.links {\n  margin-top: 14px;\n  float: right;\n}\n.link {\n  text-decoration: none;\n  display: inline-block;\n  font-size: 0.75em;\n  font-weight: 700;\n  border: 1px solid rgba(0, 148, 255, 0.3);\n  padding: 7px;\n  border-radius: 5px;\n  line-height: 14px;\n  margin: 5px;\n  text-decoration: none;\n  color: #0094ff;\n  text-transform: uppercase;\n  transition: all 200ms ease;\n}\n.link:hover {\n  background: rgba(0, 148, 255, 0.1);\n}\n.image {\n  flex: 0 0 auto;\n  width: 40%;\n  margin-right: 30px;\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: center 0;\n  border: 1px solid #dce5ef;\n  border-radius: 6px;\n  min-height: 200px;\n}\n.content {\n  display: flex;\n}\n@media only screen and (max-width: 900px) {\n  .links {\n    margin-top: 0;\n    margin-left: -5px;\n    float: none;\n    margin-bottom: 10px;\n  }\n}\n@media only screen and (max-width: 600px) {\n  :host {\n    padding: 20px;\n    padding-top: 220px;\n    position: relative;\n    border-radius: 6px 6px 0 0;\n  }\n\n  .image {\n    position: absolute;\n    top: 0;\n    left: 0;\n    height: 200px;\n    width: 100%;\n    border-radius: 6px 6px 0 0;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"